import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Image from "../components/image"

const Header = ({ siteTitle }) => (
  <header
    style={{
      background: `#292327`,
      marginBottom: `1.45rem`,
      display: "flex",
      alignItems: "center",
      justifyContent: "space-around",
    }}
  >
    <h1
      style={{ marginTop: `30px`, paddingRight: `10px`, paddingLeft: `15px` }}
    >
      <Link to="/" style={{ color: "#cc9966", textDecoration: `none` }}>
        {siteTitle}
      </Link>
    </h1>
    <div
      className="logo"
      style={{
        width: "10%",
      }}
    >
      <Image />
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
